.signup-page,
.login-page {
  @include respond-to('small') {
    margin-top: 200px;
  }
  .form-container {
    p span {
      cursor: pointer;
      color: $color-orange;
      text-decoration: underline;
    }
    .btn-container {
      .btn {
        margin: 0 7.5px;
      }
    }
  }
}
